* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'gilroy';
  /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif; */
}

body {
  height: 100%;
  width: 100%;
  background-image: url('https://wallpaperset.com/w/full/d/5/7/180759.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  /* background: linear-gradient(
    90deg,
    rgba(48, 16, 255, 1) 0%,
    rgba(100, 115, 255, 1) 100%
  ); */
}

.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  /* background: #161a2b; */
  /* From https://css.glass */
  /* From https://css.glass */
  /* From https://css.glass */
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.04);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3.4px);
  -webkit-backdrop-filter: blur(3.4px);
  border: 2px solid rgb(179 34 180 / 58%);
  text-align: center;
  /* margin: 128px auto; */

  margin: 20px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid rgb(212 10 213);
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: #16BFFD;
  background: -webkit-linear-gradient(to right, rgb(212 10 213), #16BFFD);
  background: linear-gradient(to right, rgb(212 10 213), #16BFFD);

  color: #fff;
  text-transform: capitalize;
}

.todo-input.edit {
  border: 2px solid rgb(212 10 213);
}

.todo-button.edit {
  background: #16BFFD;
  background: -webkit-linear-gradient(to right, rgb(212 10 213), #16BFFD);
  background: linear-gradient(to right, rgb(212 10 213), #16BFFD);
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(
    90deg,
    rgb(164 14 24) 0%,
    rgba(17, 122, 255, 1) 100%
  );
  
  padding: 16px;
  border-radius: 5px;
  width: 90%;
  width: 415px;
}

.todo-row:nth-child(4n + 1) {
 
  background: linear-gradient(
    90deg,
    rgb(8 47 167) 0%,
    rgba(250, 0, 135, 1) 100%
  );
  width: 415px;
}

.todo-row:nth-child(4n + 2) {
  background: linear-gradient(
    90deg,
    rgb(20 176 175)  0%,
    rgba(155, 0, 250, 1) 100%
  );
  width: 415px;
}

.todo-row:nth-child(4n + 3) {
  background: linear-gradient(
    90deg,
    rgb(37 191 116) 0%,
    rgba(255, 84, 17, 1) 100%
  );
  width: 415px;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #000;
}

.edit-icon {
  color: #000;
  font-weight: bold;
}